[line-scroll-color-mask] .line-scroll-splitted-line {
  --line-width: 100%;
  --inactive-color: var(--_theme---scroll-color-mask--inactive-color);
  --active-color: var(--_theme---scroll-color-mask--active-color);

  color: transparent;
  position: relative;
  background-color: var(--inactive-color);
  background-image: linear-gradient(
    90deg,
    var(--active-color),
    var(--active-color) var(--line-width),
    transparent var(--line-width)
  );
  background-clip: text;
}
